.app-wrapper {
    margin: 0 auto;
    width: 1200px;
    display: grid;

    grid-template-areas:
      "h h"
       "n c";
    grid-auto-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}


